.card-main {
    width: 20rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    box-shadow: -5px -5px 10px rgba(255, 255, 255, 0.5),
      2px 2px 5px rgba(94, 104, 121, 0.3);
    max-width: 100%;
    height: 400px;
    margin: 1rem auto;
    padding: 1rem;
    border-radius: 15px;
  }
  .card-title {
    font-size: 16px;
    font-weight: 500;
  }
  .card-main img {
    width: 60%;
    height: 30%;
    margin-top: 1rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 0;
  }
  .card-main .card-body h6 {
    font-size: 16px;
    font-weight: 500;
  }
  .card-main button {
    width: 100%;
    height: 40px;
    border-radius: 5px;
    margin-top: 0.5rem;
    font-weight: 400;
    letter-spacing: 1.15px;
    border: none;
    outline: none;
    cursor: pointer;
  }
  