.cart-container {
    padding: 2rem 4rem;
  }
  .cart-container h2 {
    font-weight: 400;
    font-size: 30px;
    text-align: center;
  }
  .cart-empty {
    font-size: 20px;
    margin-top: 2rem;
    color: rgb(84, 84, 84);
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .start-shopping {
    margin-top: 1rem;
  }
  .start-shopping a {
    color: gray;
    text-decoration: none;
    display: flex;
    align-items: center;
  }
  .start-shopping a span {
    margin-left: 0.5rem;
  }
  .cart-container .titles {
    margin: 2rem 0 1rem 0;
  }
  .cart-container .titles h3 {
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
  }
  .cart-container .titles {
    display: grid;
    align-items: center;
    grid-template-columns: 3fr 1fr 1fr 1fr;
    column-gap: 0.5rem;
  }
  .cart-container .titles .product-title {
    padding-left: 0.5rem;
  }
  .cart-container .titles .total {
    padding-right: 0.5rem;
    justify-self: right;
  }
  .cart-summary {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    border-top: 1px solid rgb(187, 187, 187);
    padding-top: 2rem;
  }
  .cart-summary .clear-btn {
    width: 130px;
    height: 40px;
    border-radius: 5px;
    font-weight: 400;
    letter-spacing: 1.15px;
    border: 0.5px solid rgb(177, 177, 177);
    color: gray;
    background: none;
    outline: none;
    cursor: pointer;
  }
  .cart-checkout {
    width: 270px;
    max-width: 100%;
  }
  .cart-checkout .subtotal {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
  }
  .cart-checkout .amount {
    font-weight: 700;
  }
  .cart-checkout p {
    font-size: 14px;
    font-weight: 200;
    margin: 0.5rem 0;
  }
  .cart-checkout button {
    width: 100%;
    height: 40px;
    border-radius: 5px;
    font-weight: 400;
    letter-spacing: 1.15px;
    background-color: #4b70e2;
    color: #f9f9f9;
    border: none;
    outline: none;
    cursor: pointer;
  }
  .continue-shopping {
    margin-top: 1rem;
  }
  .continue-shopping a {
    color: gray;
    text-decoration: none;
    display: flex;
    align-items: center;
  }
  .continue-shopping a span {
    margin-left: 0.5rem;
  }
  @media (max-width: 665px) {
    .cart-container {
      padding: 2rem;
    }
    .cart-container .titles {
      display: none;
    }
    .cart-container .titles {
      grid-template-columns: 1fr;
      row-gap: 1rem;
    }
     .cart-summary {
      flex-direction: column;
    }
    .cart-summary .clear-btn {
      margin-bottom: 2rem;
    }
  }