.cart-item {
    display: grid;
    align-items: center;
    grid-template-columns: 3fr 1fr 1fr 1fr;
    column-gap: 0.5rem;
    border-top: 1px solid rgb(187, 187, 187);
    padding: 1rem 0;
  }
  .cart-item .cart-product {
    display: flex;
    align-items: center;
  }
  .cart-item .cart-product img {
    width: 100px;
    max-width: 100%;
    margin-right: 1rem;
  }
  .cart-item .cart-product h3 {
    font-weight: 400;
  }
  .cart-item .cart-product .cart-product-title {
    font-size: 16px;
    font-weight: 700;
  }
  .cart-item .cart-product button {
    border: none;
    outline: none;
    margin-top: 0.7rem;
    cursor: pointer;
    background: tomato;
    padding: 5px;
    color: white;
    border-radius: 8px;
  }
  .cart-item .cart-product button:hover {
    color: black;
  }
  
  .cart-item .cart-product-quantity {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: 130px;
    max-width: 100%;
    border: 0.5px solid rgb(177, 177, 177);
    border-radius: 5px;
  }
  .cart-item .cart-product-quantity button {
    border: none;
    outline: none;
    background: none;
    padding: 0.7rem 1.5rem;
    cursor: pointer;
  }
  .cart-item .cart-product-quantity .count {
    padding: 0.7rem 0;
  }
  .cart-item .cart-product-total-price {
    padding-right: 0.5rem;
    justify-self: right;
    font-weight: 700;
  }
  @media (max-width: 665px) {
    .cart-item {
      grid-template-columns: 1fr;
      row-gap: 1rem;
    }
    .cart-item .cart-product-total-price {
      justify-self: left;
    }
  }
  